html {
    box-sizing: border-box;
}

*,
*::before,
*::after {
    box-sizing: inherit;
}

body {
    margin: 0 auto;
    padding: 0;

    font-size: 14px;
    line-height: 1.29;
    font-family: $font-base;
    color: $color-font-base;
    overflow-x: hidden;
}

a {
    text-decoration: none;
}

img {
    max-width: 100%;
    height: auto;
}

.visually-hidden {
    position: absolute;

    width: 1px;
    height: 1px;
    overflow: hidden;

    white-space: nowrap;
    /* added line */

    clip: rect(1px, 1px, 1px, 1px);
}

.tablet-only {
    display: none;
}

@media (min-width: $tablet-width) and (max-width: $desktop-width - 1) {
    .tablet-only {
        display: block;
    }
}
