.page-header {
    min-height: 66px;
    padding-top: 10px;

    border-bottom: 1px solid $color-border;
}

.page-header__inner {
    padding-right: $offset-base;
    padding-left: $offset-base;
}

.page-header__logo {
    display: inline-flex;
    padding-bottom: 16px;

    &[href]:hover .page-header__logo-image,
    &[href]:focus .page-header__logo-image {
        opacity: 0.8;
    }

    &[href]:active picture {
        opacity: 0.6;
    }
}

.page-header__logo-image {
    display: block;
    width: 191px;
    height: 39px;
}

/* --------- Tablet --------- */

@media (min-width: $tablet-width) {
    .page-header__logo {
        padding-bottom: 0;
    }

    .page-header {
        flex-direction: row;
        padding-top: 24px;
        padding-bottom: 24px;

        border-bottom-color: transparent;
    }

    .page-header__inner {
        display: flex;
        align-items: center;
        max-width: $tablet-width;
        margin: 0 auto;
        padding-right: 30px;
        padding-left: 30px;
    }

    .page-header__logo-image {
        width: 174px;
        height: 59px;
    }
}

/* --------- Desktop --------- */

@media (min-width: $desktop-width) {
    .page-header {
        padding-top: 53px;
        padding-bottom: 28px;
    }

    .page-header__inner {
        max-width: $desktop-width;
        padding: 0 40px;
    }

    .page-header__logo-image {
        width: 203px;
        height: 60px;
    }
}
